
import { FormControl } from '@angular/forms';

export function ValidateDate(c: FormControl) {
  let DATE_REGEXP =  /^\d{4}[\/\-](0?[1-9]|1[012])[\/\-](0?[1-9]|[12][0-9]|3[01])$/;

  return DATE_REGEXP.test(c.value) ? null : {
    validateDate: {
      valid: false
    }
  };
}


