import { Component, OnInit } from '@angular/core';
import { Category } from '../../../models/category';
import { DataService } from '../../../services/data.service';

@Component({
  selector: 'app-remove-category',
  templateUrl: './remove-category.component.html',
  styleUrls: ['./remove-category.component.css']
})
export class RemoveCategoryComponent implements OnInit {

  public categories: Category[] = [];
  public loading = false;
   
   constructor(private dataService: DataService) {
   }

   getCategories() {
    this.loading = true;
    this.dataService.get_categories().subscribe((res: Category[]) => {
      this.categories = res['results'];
      this.loading = false;
    });

  }

  selectRow(category){
    this.dataService.remove_category(category.category_name).subscribe((res) => {
      console.log("Cateogory deleted")
    },
      err => {
        console.log("Error occured while deleting category");
      }
    );
  }

  ngOnInit() {
    this.getCategories();
  }

}



