import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import {CardPost} from '../../../models/cardpost';
import { DataService } from '../../../services/data.service';
import { Router } from '@angular/router';
import * as $ from 'jquery';

@Component({
  selector: 'app-remove-post',
  templateUrl: './remove-post.component.html',
  styleUrls: ['./remove-post.component.css']
})
export class RemovePostComponent implements OnInit {

  public cardposts: CardPost[] = [];
  public loading = false;
  public loaded=false;

  constructor(private router:Router,private dataService: DataService) {
  }

  getPosts() {
    this.loading = true;
    this.dataService.get_posts().subscribe((res: CardPost[]) => {
      this.cardposts = res['results'];
      this.loading = false;
      this.loaded=true;
    });

  }

  ngOnInit() {
    this.getPosts();
  }


  selectRow(row){
    alert(row.slug)
    var slug=row.slug;
    this.dataService.remove_post(slug).subscribe((res) => {
      console.log("Post deleted")
    },
      err => {
        console.log("Error occured while deleting post");
      }
    );
  }
}



