import { Component, OnInit, Input,ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { FormControl, FormGroup, FormBuilder, Validators ,FormArray} from '@angular/forms';
import { DataService } from '../../../services/data.service';
import {User} from '../../../models/user';
import {Post} from '../../../models/post';
import {Category} from '../../../models/category';
import { ValidateDate} from '../../../validators/date.validator';
import { ValidateSpaces} from '../../../validators/space.validator';
import { ValidateNumbers} from '../../../validators/number.validator';
import { ValidateLetters} from '../../../validators/letter.validator';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/observable/of';
import 'rxjs/add/operator/filter';



@Component({
  selector: 'add-post',
  templateUrl: './add-post.component.html',
  styleUrls: ['./add-post.component.css']
})
export class AddPostComponent implements OnInit {

  public categories: Category[] = [];
   public users: User[] = [];

   @Input() category: Category;
  //  @ViewChild('postImage') Post_Image;
 
  //  PostImageFile : File;
   addPostForm: FormGroup;

   public editor;
   public editorContent = `<h3>I am Example content</h3>`;
   public editorOptions = {
     placeholder: "insert content...",
   };

   
   
 
   constructor(private fb: FormBuilder,private dataService: DataService) {
    
    }

    onEditorCreated(quill) {
      this.editor = quill;
      console.log('quill is ready! this is current quill instance object', quill);
    }

    
 
    createForm() {
     this.addPostForm = this.fb.group({
       title: ['', [Validators.required,Validators.minLength(4)]],
       slug:  ['', [Validators.required,ValidateSpaces, Validators.minLength(4)]],
       author:['', [Validators.required]],
       image:['', [Validators.required]],
       content:['', [Validators.required]],
       read_time: ['', [Validators.required, ValidateNumbers,Validators.minLength(4)]],
       likes:['', [ValidateNumbers]],
       category_name:['', [Validators.required]],
       publish:['', [Validators.required,ValidateDate]],
       editorContent:'',
       tags:['', [Validators.required]],
       
      //  file:''
     });

   }
  

  ngOnInit() {
    this.createForm();
    this.getCategories();
    this.getUsers();
    setTimeout(() => {
      this.editorContent = '<h1>content changed!</h1>';
      console.log('you can use the quill instance object to do something', this.editor);
      // this.editor.disable();
    }, 2800)
  }



  getCategories() {
    this.dataService.get_categories().subscribe((res: Category[]) => {
      this.categories = res['results'];
    });

  }
  
  getUsers() {
    this.dataService.get_users().subscribe((res: User[]) => {
      this.users = res['results'];
    });

  }


  onSubmit() {

    const formModel = this.addPostForm.value;


    
        // const image=this.Post_Image.nativeElement;
        // if(image.files && image.files[0]){
        //   this.PostImageFile=image.files[0];
        // }
        
        // const imageFile : File=this.PostImageFile;
        // console.log(imageFile);
    
        const formData:FormData =new FormData();
        formData.append('title',formModel.title);
        formData.append('slug',formModel.slug);
        formData.append('author',formModel.author);
        formData.append('image',formModel.image);
        formData.append('content',formModel.content);
        formData.append('read_time',formModel.read_time);
        formData.append('likes',formModel.likes);
        formData.append('category_name',formModel.category_name);
        formData.append('publish',formModel.publish);
        formData.append('tags',formModel.tags);
        // formData.append('file',imageFile,imageFile.name);
    
    
        this.dataService.add_post(formData).subscribe((res) => {
          console.log("Saved Post")
        },
          err => {
            console.log("Error occured");
          }
        );
  }

  /////////////
  onEditorBlured(quill) {
    console.log('editor blur!', quill);
  }

  onEditorFocused(quill) {
    console.log('editor focus!', quill);
  }


  onContentChanged({ quill, html, text }) {
    console.log('quill content is changed!', quill, html, text);
  }


 

}



