import { Component, OnInit, Input,ViewChild } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../../../services/data.service';
import {Category} from '../../../models/category';

@Component({
  selector: 'add-category',
  templateUrl: './add-category.component.html',
  styleUrls: ['./add-category.component.css']
})
export class AddCategoryComponent implements OnInit {

  @Input() category: Category;
  @ViewChild('categoryImage') Category_Image;

  CategoryImageFile : File;
  addCategoryForm: FormGroup;

  constructor(private fb: FormBuilder,private dataService: DataService) {
    this.createForm();
   }

   createForm() {
    this.addCategoryForm = this.fb.group({
      category_name: ['', Validators.required ],
      category_image: '',
    });
  }

  getCategoryname(){
    const nameControl = this.addCategoryForm.get('category_name');
  }

  onSubmit() {
    const formModel = this.addCategoryForm.value;
    
        // const image=this.Category_Image.nativeElement;
        // if(image.files && image.files[0]){
        //   this.CategoryImageFile=image.files[0];
        // }
        
        // const imageFile : File=this.CategoryImageFile;
        // console.log(imageFile);
    
        const formData:FormData =new FormData();
        formData.append('category_name',formModel.category_name);
        formData.append('category_image',formModel.category_image);
        // formData.append('file',imageFile,imageFile.name);
    
    
        this.dataService.add_category(formData).subscribe((res) => {
          console.log("Saved Category")
        },
          err => {
            console.log("Error occured while save category");
          }
        );
  }

  ngOnInit() {
  }

}



