import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { HttpClientModule } from '@angular/common/http';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule,ReactiveFormsModule } from '@angular/forms';


import { AuthGuard } from './guard/auth.guard';

import { DataService } from './services/data.service';
import { NoopInterceptor } from './services/noop.Interceptor';

import { AppComponent } from './app.component';

import { UserListComponent } from './components/user-list/user-list.component';
import { CategoryListComponent } from './components/category-list/category-list.component';
import { PostListComponent } from './components/post-list/post-list.component';
import { HomeComponent } from './components/home/home.component';

import { NavbarComponent } from './shared/navbar/navbar.component';

import { LoginComponent } from './components/authorized/login/login.component';
import { RegisterComponent } from './components/authorized/register/register.component';
import { AddPostComponent } from './components/authorized/add-post/add-post.component';
import { DashboardComponent } from './components/authorized/dashboard/dashboard.component';
import { AddCategoryComponent } from './components/authorized/add-category/add-category.component';
import { ViewPostComponent } from './components/view-post/view-post.component';

// import { FroalaEditorModule, FroalaViewModule } from 'angular-froala-wysiwyg';
import {NgxPaginationModule} from 'ngx-pagination'; 
import { LoadingModule,ANIMATION_TYPES  } from 'ngx-loading'; 
import { QuillEditorModule } from 'ngx-quill-editor';
import { TagInputModule } from 'ngx-chips';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'; // this is needed!

import { SafeHtml } from './pipes/SafeHtml';
import { RemovePostComponent } from './components/authorized/remove-post/remove-post.component';
import { RemoveCategoryComponent } from './components/authorized/remove-category/remove-category.component';
import { FooterComponent } from './shared/footer/footer.component';



const appRoutes: Routes = [
  {
    path: 'dashboard',
    canActivate: [AuthGuard],
    component: DashboardComponent
  },
  {
    path: 'addPost',
    component: AddPostComponent
  },
  {
    path: 'addCategory',
    component: AddCategoryComponent
  },
  {
    path: '',
    component: HomeComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'register',
    component: RegisterComponent
  },
  {
    path: 'post/:slug',
    component: ViewPostComponent
  },
  {
    path: 'removePost',
    component: RemovePostComponent
  },
  {
    path: 'removeCategory',
    component: RemoveCategoryComponent
  }
];

@NgModule({
  declarations: [
    AppComponent,
    UserListComponent,
    CategoryListComponent,
    PostListComponent,
    AddPostComponent,
    NavbarComponent,
    LoginComponent,
    DashboardComponent,
    RegisterComponent,
    HomeComponent,
    AddCategoryComponent,
    ViewPostComponent,
    SafeHtml,
    RemovePostComponent,
    RemoveCategoryComponent,
    FooterComponent
  ],
  imports: [
    RouterModule.forRoot(appRoutes),
    BrowserModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    NgxPaginationModule,
    QuillEditorModule,
    TagInputModule, 
    BrowserAnimationsModule,
    NgbModule.forRoot(),
    LoadingModule.forRoot({
      animationType: ANIMATION_TYPES.threeBounce,
      backdropBackgroundColour: 'rgba(0,0,0,0.1)', 
      backdropBorderRadius: '4px',
      primaryColour: '#ffffff', 
      secondaryColour: '#ffffff', 
      tertiaryColour: '#ffffff'
  })
    // FroalaEditorModule.forRoot(), FroalaViewModule.forRoot()
  ],
  providers: [AuthGuard, DataService, {
    provide: HTTP_INTERCEPTORS,
    useClass: NoopInterceptor,
    multi: true,
  }],
  bootstrap: [AppComponent]
})
export class AppModule { }



