import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders,HttpParams } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Observable } from "rxjs/Observable";

import { Category } from '../models/category';
import { Post } from '../models/post';




@Injectable()
export class DataService {

    //get api endpoint url fron environment file
    baseUrl: string = environment.apiEndpoint;

    private isUserLoggedIn;
    private username;

    constructor(private httpClient: HttpClient) {
        this.isUserLoggedIn = false;
    }

    setUserLoggedIn() {
        this.isUserLoggedIn = true;
    }

    getUserLoggedIn() {
        return this.isUserLoggedIn;
    }

    add_post(formGroup) {
        return this.httpClient.post<Post>(this.baseUrl + '/addPost', formGroup);
    }

    add_category(formGroup) {
        return this.httpClient.post<Category>(this.baseUrl + '/addCategory', formGroup);
    }

    login_user(email, password) {
        return this.httpClient.post(this.baseUrl + '/login', {
            email: email,
            password: password
        });
    }

    register_user() {
        return this.httpClient.get(this.baseUrl + '/signup');
    }

    get_users() {
        return this.httpClient.get(this.baseUrl + '/users');
    }

    get_posts() {
        return this.httpClient.get(this.baseUrl + '/posts');
    }

    get_categories() {
        return this.httpClient.get(this.baseUrl + '/categories');
    }

    view_post(slug) {
        return this.httpClient.get(this.baseUrl + '/posts/'+slug);
        
    }

    remove_post(slug){
        return this.httpClient.delete(this.baseUrl+'/posts/removePost/'+slug);
    }

    remove_category(category_name){
        return this.httpClient.delete(this.baseUrl+'/categories/removeCategory/'+category_name);
    }

}


