import { FormControl } from '@angular/forms';

export function ValidateSpaces(c: FormControl) {
  let SPACES_REGEXP =/^\S*$/;

  return SPACES_REGEXP.test(c.value) ? null : {
    validateSpaces: {
      valid: false
    }
  };
}


