import { FormControl } from '@angular/forms';

export function ValidateNumbers(c: FormControl) {
  let NUMBERS_REGEXP =/^\d+$/;

  return NUMBERS_REGEXP.test(c.value) ? null : {
    validateNumbers: {
      valid: false
    }
  };
}


